import { useContext, useRef, useReducer, useState } from "react";
import {
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Button,
  IconButton,
  OutlinedInput,
  InputAdornment,
  Modal,
  Box,
  Typography,
  TextField,
  Autocomplete,
} from "@mui/material";
import LoadingButton from "@mui/lab/LoadingButton";
import AddIcon from "@mui/icons-material/Add";
import CloseIcon from "@mui/icons-material/Close";
import Swal from "sweetalert2";
import { brown } from "@mui/material/colors";
import { AuthContext } from "../../../../components/AuthContext";
import SearchIcon from "@mui/icons-material/Search";
import { useMutation, useQuery } from "react-query";
import Table from "../../../../components/Table";
import { setNewStateValue } from "./PostDateChecks";

const warehouseColumns = [
  { field: "PNo", headerName: "PN No.", minWidth: 130 },
  { field: "IDNo", headerName: "I.D. No", minWidth: 130 },
  {
    field: "dateRecieved",
    headerName: "Date Received",
    minWidth: 120,
  },
  { field: "Name", headerName: "Name", flex: 1, minWidth: 350 },
  { field: "Check_Date", headerName: "Check Date", minWidth: 120 },
  { field: "Check_No", headerName: "Check No.", minWidth: 120 },
  { field: "Check_Amnt", headerName: "Check", minWidth: 130, type: "number" },
  { field: "Bank", headerName: "Bank", minWidth: 100 },
  { field: "PDC_Status", headerName: "PDC Status", minWidth: 100 },
  { field: "PDC_ID", headerName: "PDC_ID", minWidth: 100, hide: true },
];
const initialState = {
  pdcStatus: "",
  searchType: "",
  searchBy: "IDNo",
  remarks: "",
  search: "",
  warehouseMode: "",
  modalRCPNoSearch: "",
  pdcStatusDisable: false,
  pdcStatusDisableOnSearch: false,
};
export const reducer = (state: any, action: any) => {
  switch (action.type) {
    case "UPDATE_FIELD":
      return {
        ...state,
        [action.field]: action.value,
      };
    default:
      return state;
  }
};

export default function WarehouseChecks() {
  const refParent = useRef<HTMLDivElement>(null);
  const [showModalPullout, setShowModalPullout] = useState(false);
  const { myAxios, user } = useContext(AuthContext);
  const [warehouseRows, setWarehouseRows] = useState<any>([]);
  const [pullout, setPullout] = useState<any>([]);
  const [pulloutRCPN, setPulloutRCPN] = useState<Array<any>>([]);
  const [state, dispatch] = useReducer(reducer, initialState);
  const table = useRef<any>(null);
  const tablePullout = useRef<any>(null);

  const { isLoading: loadingPulloutRequestId, mutate: mutateLoadRCPN } =
    useMutation({
      mutationKey: "pullout-rcpn-approved",
      mutationFn: async (variable: any) =>
        await myAxios.post(
          `/task/accounting/pullout/approved/load-rcpn-approved`,
          variable,
          {
            headers: {
              Authorization: `Bearer ${user?.accessToken}`,
            },
          }
        ),
      onSuccess: (data) => {
        const response = data as any;
        setPulloutRCPN(response.data.rcpn);
      },
    });

  const {
    isLoading: loadingPulloutApprovedList,
    mutate: mutatePulloutApprovedList,
  } = useMutation({
    mutationKey: "pullout-approved-list",
    mutationFn: async (variable: any) =>
      await myAxios.post(
        `/task/accounting/pullout/approved/load-rcpn-approved-list`,
        variable,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      ),
    onSuccess: (data) => {
      const response = data as any;
      setPullout(response.data.rcpnList);
    },
  });

  const { isLoading: isLoadingCheckSearch, mutate: mutaterCheckSearch } =
    useMutation({
      mutationKey: "check-serach",
      mutationFn: async (variables: any) =>
        myAxios.post(
          "/task/accounting/warehouse/search-pdc-checks-client-policy",
          variables,
          {
            headers: {
              Authorization: `Bearer ${user?.accessToken}`,
            },
          }
        ),
      onSuccess(response) {
        console.log(response.data.data);
        setWarehouseRows(response.data.data);
      },
    });

  const {
    isLoading: isLoadingSearchCheckListApprovedPullout,
    refetch: refetchApprovedPulloutWarehouse,
  } = useQuery({
    queryKey: "search-approved-pullout-checklist",
    queryFn: async () =>
      await myAxios.get(
        `/task/accounting/warehouse/search-checklist-approved-pullout-warehouse?searchApprovedPulloutCheckList=${state.modalRCPNoSearch}`,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      ),
    refetchOnWindowFocus: false,
    onSuccess(res) {
      setPullout(res.data.data);
    },
  });

  const { isLoading: isLoadingApprovedPulloutWarehouse } = useQuery({
    queryKey: "search-approved-pullout",
    queryFn: async () =>
      await myAxios.get(
        `/task/accounting/warehouse/search-approved-pullout-warehouse?searchApprovedPullout=`,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      ),
    refetchOnWindowFocus: false,
  });
  const {
    mutate: mutateSelectecCheckInPullout,
    isLoading: isLoadingSelectecCheckInPullout,
  } = useMutation({
    mutationKey: "selected-check",
    mutationFn: async (variable: any) =>
      await myAxios.post(
        "/task/accounting/warehouse/search-checklist-approved-pullout-warehouse-selected",
        variable,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      ),
    onSuccess: (res) => {
      setWarehouseRows([]);
      setWarehouseRows(res?.data.data);
    },
  });
  const { mutate, isLoading } = useMutation({
    mutationKey: "selected-check",
    mutationFn: async (variable: any) =>
      await myAxios.post(
        "/task/accounting/warehouse/get-search-selected-pdc-checks-client-policy",
        variable,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      ),
    onSuccess: (res) => {
      setWarehouseRows([]);
      setWarehouseRows(res?.data.data);
      // closePolicyIdClientIdRefId();
    },
  });

  const { mutate: saveMutate, isLoading: saveLoading } = useMutation({
    mutationKey: "save-warehouse",
    mutationFn: async (variable: any) =>
      await myAxios.post("/task/accounting/warehouse/save", variable, {
        headers: {
          Authorization: `Bearer ${user?.accessToken}`,
        },
      }),
    onSuccess: (response: any) => {
      if (response.data.success) {
        // const selected = table.current.getSelectedRows();
        // setWarehouseRows((w: any) => {
        //   return w.filter(
        //     (item: any) =>
        //       !selected.map((items: any) => items.PDC_ID).includes(item.PDC_ID)
        //   );
        // });
        setNewStateValue(dispatch, initialState);
        setWarehouseRows([]);
        setPullout([]);
        return Swal.fire({
          position: "center",
          icon: "success",
          title: response.data.message,
          showConfirmButton: false,
          timer: 1500,
        });
      }
      return Swal.fire({
        position: "center",
        icon: "error",
        title: response.data.message,
        showConfirmButton: false,
        timer: 1500,
      });
    },
  });

  function searchEnter(search: Array<any>) {
    mutate({ ...state, ...search[0] });

    dispatch({
      type: "UPDATE_FIELD",
      field: "pdcStatusDisable",
      value: false,
    });
    dispatch({
      type: "UPDATE_FIELD",
      field: "pdcStatusDisableOnSearch",
      value: true,
    });
  }

  const handleInputChange = (e: any) => {
    const { name, value } = e.target;
    dispatch({ type: "UPDATE_FIELD", field: name, value });
  };

  const handleOnSave = () => {
    const messages = [
      "stored in warehouse?",
      "endorse for deposit?",
      "pulled out?",
    ];
    Swal.fire({
      title: "Are you sure?",
      text:
        "Do you want the check(s) to be " + messages[parseInt(state.pdcStatus)],
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, save it!",
    }).then((result) => {
      if (result.isConfirmed) {
        if (state.remarks === "") {
          return Swal.fire({
            position: "center",
            icon: "warning",
            title: "Please provide remarks!",
            timer: 3000,
          });
        }

        if (state.pdcStatus === "2" && state.remarks === "") {
          return Swal.fire({
            position: "center",
            icon: "warning",
            title: "Please provide remarks!",
            timer: 3000,
          });
        }
        if (warehouseRows.length <= 0) {
          return Swal.fire({
            position: "center",
            icon: "warning",
            title: "No current record",
            timer: 3000,
          });
        }

        const selectedList = table.current.getSelectedRows();
        if (selectedList.length <= 0 && !state.pdcStatusDisable) {
          return Swal.fire({
            position: "center",
            icon: "warning",
            title: "Please select from list",
            timer: 3000,
          });
        }

        if (state.pdcStatusDisable) {
          saveMutate({
            ...state,
            selected: JSON.stringify(warehouseRows),
          });
        } else {
          saveMutate({
            ...state,
            selected: JSON.stringify(selectedList),
          });
        }
      }
    });
  };

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        width: "100%",
        height: "100%",
        flex: 1,
      }}
    >
      <div
        style={
          {
            height: "70px",
            display: "flex",
            columnGap: "50px",
          } as any
        }
      >
        <div
          style={
            {
              display: "flex",
              flex: 1,
              flexDirection: "column",
              gap: "10px  ",
            } as any
          }
        >
          <div style={{ display: "flex", columnGap: "10px" }}>
            <FormControl
              disabled={
                state.warehouseMode !== "add" ||
                state.pdcStatusDisableOnSearch ||
                state.pdcStatusDisable
              }
              size="small"
              variant="outlined"
              sx={{
                flex: 1,
                ".MuiFormLabel-root": {
                  fontSize: "14px",
                  background: "white",
                  zIndex: 99,
                  padding: "0 3px",
                },
                ".MuiFormLabel-root[data-shrink=false]": { top: "-5px" },
              }}
            >
              <InputLabel id="label-selection-reason">PDC Status</InputLabel>
              <Select
                labelId="label-selection-reason"
                value={state.pdcStatus}
                name="pdcStatus"
                onChange={(e) => {
                  handleInputChange(e);
                  if (e.target.value === "2") {
                    return dispatch({
                      type: "UPDATE_FIELD",
                      field: "remarks",
                      value: "full_paid",
                    });
                  } else {
                    return dispatch({
                      type: "UPDATE_FIELD",
                      field: "remarks",
                      value: "",
                    });
                  }
                }}
                autoWidth
                sx={{
                  height: "27px",
                  fontSize: "14px",
                }}
              >
                <MenuItem value=""></MenuItem>
                <MenuItem value="0">Stored in Warehouse</MenuItem>
                <MenuItem value="1">Endorse for Deposit</MenuItem>
                <MenuItem value="2">Pull Out</MenuItem>
              </Select>
            </FormControl>
            <FormControl
              size="small"
              variant="outlined"
              disabled={
                state.pdcStatus !== "2" || state.warehouseMode !== "add"
              }
              sx={{
                flex: 1,
                ".MuiFormLabel-root": {
                  fontSize: "14px",
                  background: "white",
                  zIndex: 99,
                  padding: "0 3px",
                },
                ".MuiFormLabel-root[data-shrink=false]": { top: "-5px" },
              }}
            >
              <InputLabel id="remarks">Remarks</InputLabel>
              <Select
                labelId="remarks"
                value={state.remarks}
                name="remarks"
                onChange={handleInputChange}
                autoWidth
                sx={{
                  height: "27px",
                  fontSize: "14px",
                }}
              >
                <MenuItem value=""></MenuItem>
                <MenuItem value="Fully Paid">Fully Paid</MenuItem>
                <MenuItem value="Cash Replacement">Cash Replacement</MenuItem>
                <MenuItem value={"Check Replacement"}>
                  Check Replacement{" "}
                </MenuItem>
                <MenuItem value={"Account Closed"}>Account Closed </MenuItem>
                <MenuItem value={"Hold"}>Hold </MenuItem>
                <MenuItem value={"Not Renewed by"}>Not Renewed by </MenuItem>
              </Select>
            </FormControl>
          </div>
          <div style={{ display: "flex", columnGap: "10px" }}>
            <FormControl
              size="small"
              variant="outlined"
              disabled={state.warehouseMode !== "add"}
              sx={{
                flex: 1,
                ".MuiFormLabel-root": {
                  fontSize: "14px",
                  background: "white",
                  zIndex: 99,
                  padding: "0 3px",
                },
                ".MuiFormLabel-root[data-shrink=false]": { top: "-5px" },
              }}
            >
              <InputLabel id="search-type">Search Type</InputLabel>
              <Select
                labelId="search-type"
                value={state.searchType}
                name="searchType"
                onChange={handleInputChange}
                autoWidth
                sx={{
                  height: "27px",
                  fontSize: "14px",
                }}
              >
                <MenuItem value=""></MenuItem>
                <MenuItem value={"0"}>Policy </MenuItem>
                <MenuItem value={"1"}>ID No.</MenuItem>
                <MenuItem value="2">Account Name</MenuItem>
                <MenuItem value="3">Bank</MenuItem>
              </Select>
            </FormControl>
            {isLoadingCheckSearch ? (
              <LoadingButton loading={isLoadingCheckSearch} />
            ) : (
              <FormControl
                variant="outlined"
                size="small"
                sx={{
                  flex: 1,
                  ".MuiFormLabel-root": {
                    fontSize: "14px",
                    background: "white",
                    zIndex: 99,
                    padding: "0 3px",
                  },
                  ".MuiFormLabel-root[data-shrink=false]": { top: "-5px" },
                }}
                disabled={state.warehouseMode !== "add"}
              >
                <InputLabel htmlFor="Search">Search</InputLabel>
                <OutlinedInput
                  sx={{
                    height: "27px",
                    fontSize: "14px",
                  }}
                  fullWidth
                  label="Search"
                  name="search"
                  value={state.search}
                  onChange={handleInputChange}
                  onKeyDown={(e) => {
                    if (e.code === "Enter" || e.code === "NumpadEnter") {
                      e.preventDefault();
                      if (state.pdcStatus === "") {
                        return Swal.fire({
                          position: "center",
                          icon: "warning",
                          title: "Please provide status!",
                          showConfirmButton: false,
                          timer: 1500,
                        });
                      }

                      if (state.searchType === "") {
                        return Swal.fire({
                          position: "center",
                          icon: "warning",
                          title: "Please select search type!",
                          showConfirmButton: false,
                          timer: 1500,
                        });
                      }

                      if (state.search === "") {
                        return Swal.fire({
                          position: "center",
                          icon: "warning",
                          title: "Type field you want to search!",
                          showConfirmButton: false,
                          timer: 1500,
                        });
                      }

                      mutaterCheckSearch(state);
                    }
                  }}
                  id="policy-client-ref-id"
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton
                        disabled={state.warehouseMode !== "add"}
                        aria-label="search-client"
                        color="secondary"
                        edge="end"
                        onClick={() => {
                          if (state.pdcStatus === "") {
                            return Swal.fire({
                              position: "center",
                              icon: "warning",
                              title: "Please provide status!",
                              showConfirmButton: false,
                              timer: 1500,
                            });
                          }

                          if (state.searchType === "") {
                            return Swal.fire({
                              position: "center",
                              icon: "warning",
                              title: "Please select search type!",
                              showConfirmButton: false,
                              timer: 1500,
                            });
                          }

                          if (state.search === "") {
                            return Swal.fire({
                              position: "center",
                              icon: "warning",
                              title: "Type field you want to search!",
                              showConfirmButton: false,
                              timer: 1500,
                            });
                          }

                          mutaterCheckSearch(state);
                        }}
                      >
                        <SearchIcon />
                      </IconButton>
                    </InputAdornment>
                  }
                />
              </FormControl>
            )}
          </div>
        </div>
        <div style={{ display: "flex", flex: 1 } as any}>
          <div
            style={{
              display: "flex",
              alignItems: "flex-end",
              columnGap: "10px",
              paddingBottom: "5px",
            }}
          >
            {state.warehouseMode === "" && (
              <Button
                sx={{
                  height: "30px",
                  fontSize: "11px",
                }}
                variant="contained"
                startIcon={<AddIcon sx={{ width: 15, height: 15 }} />}
                id="entry-header-save-button"
                onClick={() => {
                  handleInputChange({
                    target: { value: "add", name: "warehouseMode" },
                  });
                }}
                color="primary"
              >
                New
              </Button>
            )}
            <LoadingButton
              sx={{
                height: "30px",
                fontSize: "11px",
              }}
              disabled={state.warehouseMode === ""}
              onClick={handleOnSave}
              color="success"
              variant="contained"
            >
              Save
            </LoadingButton>
            {state.warehouseMode !== "" && (
              <LoadingButton
                sx={{
                  height: "30px",
                  fontSize: "11px",
                }}
                variant="contained"
                startIcon={<CloseIcon sx={{ width: 15, height: 15 }} />}
                color="error"
                onClick={() => {
                  Swal.fire({
                    title: "Are you sure?",
                    text: "You won't be able to revert this!",
                    icon: "warning",
                    showCancelButton: true,
                    confirmButtonColor: "#3085d6",
                    cancelButtonColor: "#d33",
                    confirmButtonText: "Yes, cancel it!",
                  }).then((result) => {
                    if (result.isConfirmed) {
                      setNewStateValue(dispatch, initialState);
                      table.current?.removeSelection();
                      setWarehouseRows([]);
                    }
                  });
                }}
              >
                Cancel
              </LoadingButton>
            )}
            <LoadingButton
              sx={{
                height: "30px",
                fontSize: "11px",
              }}
              disabled={
                state.warehouseMode === "" || state.warehouseMode === "add"
              }
              // onClick={handleOnSave}
              color="success"
              variant="contained"
            >
              Delete
            </LoadingButton>
            <LoadingButton
              sx={{
                height: "30px",
                fontSize: "11px",
                background: brown[500],
                ":hover": {
                  background: brown[600],
                },
              }}
              variant="contained"
              onClick={() => {
                setShowModalPullout(true);
                mutatePulloutApprovedList({ RCPN: "" });
                mutateLoadRCPN({});
              }}
              disabled={state.warehouseMode === ""}
            >
              Check for pull-out
            </LoadingButton>
          </div>
        </div>
      </div>
      <div
        ref={refParent}
        style={{
          marginTop: "10px",
          width: "100%",
          position: "relative",
          flex: 1,
        }}
      >
        <Box
          style={{
            height: `${refParent.current?.getBoundingClientRect().height}px`,
            width: "100%",
            overflowX: "scroll",
            position: "absolute",
          }}
        >
          <Table
            ref={table}
            isLoading={
              isLoading || saveLoading || isLoadingSelectecCheckInPullout
            }
            columns={warehouseColumns}
            rows={warehouseRows}
            table_id={"PDC_ID"}
            isSingleSelection={false}
            isRowFreeze={false}
            checkboxSelection={!state.pdcStatusDisable}
          />
        </Box>
      </div>
      <Modal
        open={showModalPullout}
        onClose={() => {
          setShowModalPullout(false);
        }}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box
          sx={{
            position: "absolute" as "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            bgcolor: "background.paper",
            p: 4,
          }}
        >
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Pull Out Viewer
          </Typography>
          <br />
          <div
            style={{
              display: "flex",
              columnGap: "10px",
              width: "800px",
              height: "500px",
              flexDirection: "column",
            }}
          >
            {isLoadingApprovedPulloutWarehouse ? (
              <LoadingButton loading={isLoadingApprovedPulloutWarehouse} />
            ) : (
              <Autocomplete
                loading={loadingPulloutRequestId}
                freeSolo
                options={pulloutRCPN.map((itm: any) => itm.RCPNo)}
                value={state.modalRCPNoSearch}
                onChange={(e, v: any) => {
                  if (v) {
                    dispatch({
                      type: "UPDATE_FIELD",
                      field: "modalRCPNoSearch",
                      value: v,
                    });

                    mutatePulloutApprovedList({ RCPN: v });
                  }
                }}
                onInput={(e: any) => {
                  dispatch({
                    type: "UPDATE_FIELD",
                    field: "modalRCPNoSearch",
                    value: e.target.value,
                  });
                }}
                onBlur={(e) => {
                  const options = pulloutRCPN;
                  const find = options.find(
                    (itm) => itm.RCPNo === state.modalRCPNoSearch
                  );
                  if (find !== undefined) {
                    dispatch({
                      type: "UPDATE_FIELD",
                      field: "modalRCPNoSearch",
                      value: find.RCPNo,
                    });
                    mutatePulloutApprovedList({ RCPN: find.RCPNo });
                  }
                }}
                onKeyDown={(e) => {
                  if (e.code === "NumpadEnter" || e.code === "Enter") {
                    e.preventDefault();
                    mutatePulloutApprovedList({ RCPN: state.modalRCPNoSearch });
                  }
                }}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    InputProps={{
                      ...params.InputProps,
                      style: { height: "27px", fontSize: "14px" },
                    }}
                    label="PN No."
                  />
                )}
                sx={{
                  width: "100%",
                  ".MuiFormLabel-root": {
                    fontSize: "14px",
                  },
                  ".MuiInputBase-input": {
                    width: "100% !important",
                  },
                  ".MuiFormLabel-root[data-shrink=false]": {
                    top: "-5px",
                  },
                  ".MuiAutocomplete-input ": {
                    position: "absolute",
                  },
                }}
                size="small"
              />
            )}
            <div
              style={{
                marginTop: "10px",
                width: "100%",
                position: "relative",
              }}
            >
              <Box
                style={{
                  height: "450px",
                  width: "100%",
                  overflowX: "scroll",
                  position: "absolute",
                }}
              >
                <Table
                  ref={tablePullout}
                  isLoading={loadingPulloutApprovedList}
                  columns={[
                    { field: "RCPNo", headerName: "RCP No.", width: 150 },
                    { field: "PNNo", headerName: "PN No.", width: 150 },
                    {
                      field: "Name",
                      headerName: "Name",
                      flex: 1,
                      minWidth: 300,
                    },
                    {
                      field: "NoOfChecks",
                      headerName: "No. of Checks",
                      width: 100,
                    },
                    {
                      field: "Reason",
                      headerName: "Reason",
                      flex: 1,
                      minWidth: 300,
                    },
                  ]}
                  rows={pullout}
                  table_id={"RCPNo"}
                  isSingleSelection={true}
                  isRowFreeze={false}
                  dataSelection={(selection, data, code) => {
                    const rowSelected = data.filter(
                      (item: any) => item.RCPNo === selection[0]
                    )[0];

                    if (rowSelected === undefined || rowSelected.length <= 0) {
                      return;
                    }

                    dispatch({
                      type: "UPDATE_FIELD",
                      field: "pdcStatus",
                      value: "2",
                    });
                    dispatch({
                      type: "UPDATE_FIELD",
                      field: "pdcStatusDisable",
                      value: true,
                    });
                    dispatch({
                      type: "UPDATE_FIELD",
                      field: "remarks",
                      value: rowSelected.Reason,
                    });
                    mutateSelectecCheckInPullout({
                      RCPNo: selection[0],
                    });
                    setShowModalPullout(false);
                  }}
                />
              </Box>
            </div>
            <IconButton
              style={{
                position: "absolute",
                top: "10px",
                right: "10px",
              }}
              aria-label="search-client"
              onClick={() => {
                setShowModalPullout(false);
              }}
            >
              <CloseIcon />
            </IconButton>
          </div>
        </Box>
      </Modal>
      {/* {ModalPolicyIdClientIdRefId} */}
    </div>
  );
}
